.header {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	background-color: $dark-gr;
	padding-left: 40px;
	padding-right: 40px;
	height: 48px;
	align-items: center;
	color: $black;
	border-bottom: 4px solid $green;
	font-size: 25px;
	&__logo {
		font-weight: 600;
	}
}
